@import "grid";

$pipBackgroundColor: black;
$pipForegroundColor: white;

$pipScreenWidth: 800px;
$pipScreenHeight: 640px;

$pipFont: "Share Tech Mono";
$pipFontsize: 20px;


@font-face {
	font-family: $pipFont;
	font-style: normal;
	font-weight: 400;
	src: local('Share Tech Mono'), local('ShareTechMono-Regular'), url(/fonts/ShareTechMono.woff2) format('woff2');
	unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2212, U+2215, U+E0FF, U+EFFD, U+F000;
}


html, body{
	height: 100%;
	background: $pipBackgroundColor;
	color: $pipForegroundColor;
	font-size: $pipFontsize;
	font-family: $pipFont;
	margin: 0;
	padding: 0;
}

#app{
	width: $pipScreenWidth;
	height: $pipScreenHeight;
	display: grid;
	grid-template-columns: ($pipScreenWidth / 6) ($pipScreenWidth / 6) ($pipScreenWidth / 6) ($pipScreenWidth / 6) ($pipScreenWidth / 6) ($pipScreenWidth / 6);
	grid-template-rows: ($pipScreenHeight / 6) ($pipScreenHeight / 6) ($pipScreenHeight / 6) ($pipScreenHeight / 6) ($pipScreenHeight / 6) ($pipScreenHeight / 6);
}

.block-lights{
	grid-column: 1 / 5;
	grid-row: 1/ 3;
	background: lighten($pipBackgroundColor, 10%);
}